@charset "UTF-8";
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300, 400, 700);

body {
  font-family: 'Open Sans', sans-serif;
  font-weight: 300;
  line-height: 1.42em;
  color: #a7a1ae;
  background-color: #1f2739;
}

h1 {
  font-size: 3em;
  font-weight: 300;
  line-height: 1em;
  text-align: center;
  color: #4dc3fa;
}

.container th h1 {
  font-weight: bold;
  font-size: 1em;
  text-align: left;
  color: #185875;
}

.container td {
  font-weight: normal;
  font-size: 1em;
  -webkit-box-shadow: 0 2px 2px -2px #0e1119;
  box-shadow: 0 2px 2px -2px #0e1119;
}

.container {
  text-align: left;
  overflow: hidden;
  width: 80%;
  margin: 0 auto;
  display: table;
}

.container td,
.container th {
  padding-bottom: 2%;
  padding-top: 2%;
  padding-left: 2%;
}

/* Background-color of the odd rows */
.container tr:nth-child(odd) {
  background-color: #323c50;
}

/* Background-color of the even rows */
.container tr:nth-child(even) {
  background-color: #2c3446;
}

.container th {
  background-color: #1f2739;
}

.container td > a:first-child {
  color: #fb667a;
}
.container td > a:hover {
  color: #403e10;
}

.container tr:hover {
  background-color: #464a52;
}

.container td:hover {
  background-color: #fff842;
  color: #403e10;
}

@media screen and (max-width: 600px) {
  table thead {
    clip: rect(0 0 0 0);
    overflow: hidden;
    position: absolute;
  }
  table tr {
    display: block;
  }
  table td {
    display: block;
  }
  table td:before {
    content: attr(data-label);
    float: left;
    text-transform: uppercase;
  }
}

.container td {
  text-transform: capitalize;
}

.move {
  margin-top: 5px;
  color: #fff;
  font-size: 20px;
}

.move-left {
  float: left;
}

.move-right {
  float: right;
}



